* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s ease-in-out;
}

.card__image {
  object-fit: contain;
  object-position: center;
  width: 100%;
  height: 134px;
  margin-bottom: 40px;
}

.card__description {
  letter-spacing: 0%;
  color: #060b35;
  align-self: center;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  align-self: flex-start;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
  display: flex;
}

.card__review-reviews {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-top: 0;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-price {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  width: 100%;
  height: 38px;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card:hover .card__description {
  color: #34568b;
}

.stars {
  display: flex;
  position: relative;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  position: relative;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 60px;
  display: block;
}

.nav__link {
  color: #060b35;
  line-height: 60px;
  font-style: 12px;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  margin: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 100vw;
  max-width: 100%;
  display: flex;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
